import { useEffect, useState } from "react"
import ButtonBack from "../Components/Buttons/ButtonBack"
import PageContent from "../Components/PageContent"
import PageTitle from "../Components/PageTitle"
import { useNavigate, useParams } from "react-router-dom"
import FormGroup from "../../Components/FormGroup"
import { get, put } from "../../Functions/Api"
import { PopupAlert } from "../../Functions/Alerts"
import SelectInput from "../../Components/SelectInput"
import FormCheck from "../../Components/FormCheck"
import Card from "../../Components/Card"
import FormTextarea from "../../Components/FormTextarea"
import useInfoKelurahan from "../../Hooks/useInfoKelurahan"
import SelectOptionAgama from "../Components/SelectsOption/SelectOptionAgama"
import ButtonSubmit from "../Components/Buttons/ButtonSubmit"

const EditAnggota = () => {
    const [detailAnggotaKK, setDetailAnggotaKK] = useState<DataT>()
    const { no_kk, nik, type } = useParams()
    const navigate = useNavigate()
    const infoKelurahan = useInfoKelurahan()

    const editAnggota = async (evt: React.FormEvent<HTMLFormElement>) => {
        evt.preventDefault()

        const res = await put(`./anggota-keluarga/${nik}`, evt.currentTarget)
        if (res.ok) {
            navigate(`/${type}/data-keluarga/${no_kk}/${nik}`)
            PopupAlert.fire({
                icon: "success",
                text: "Berhasil Menyimpan Data"
            })
        } else {
            window.scrollTo(0, 0)
        }
    }

    useEffect(() => {
        const getDetailAnggotaKK = async () => {
            const res = await get(`anggota-keluarga/${nik}?no_kk=${no_kk}`)
            if (res.ok) setDetailAnggotaKK(res.data)
        }
        getDetailAnggotaKK()
    }, [nik, no_kk])

    return (
        <>
            <PageTitle title="Data Keluarga" />
            <ButtonBack />
            <PageContent>
                {detailAnggotaKK && (
                    <Card
                        cardHeader={
                            <h4 className="card-title text-success my-3">
                                Update Anggota Keluarga
                            </h4>
                        }
                        cardBody={
                            <form onSubmit={editAnggota}>
                                <input
                                    type="hidden"
                                    value={no_kk}
                                    name="no_kk"
                                />
                                <div className="row">
                                    <div className="col-md-6">
                                        <FormGroup
                                            label="Nama"
                                            type="text"
                                            id="nama"
                                            name="nama"
                                            defaultValue={detailAnggotaKK.nama}
                                        />
                                        <FormGroup
                                            label="NIK"
                                            type="number"
                                            id="nik"
                                            name="nik"
                                            defaultValue={detailAnggotaKK.nik}
                                        />
                                        <div className="row">
                                            <div className="col-md-6">
                                                <FormGroup
                                                    label="Tempat Lahir"
                                                    type="text"
                                                    id="tempat_lahir"
                                                    name="tempat_lahir"
                                                    defaultValue={
                                                        detailAnggotaKK.tempat_lahir ||
                                                        "-"
                                                    }
                                                />
                                            </div>
                                            <div className="col-md-6">
                                                <FormGroup
                                                    label="Tanggal Lahir"
                                                    type="date"
                                                    id="tanggal_lahir"
                                                    name="tanggal_lahir"
                                                    defaultValue={
                                                        detailAnggotaKK.tanggal_lahir ||
                                                        "2000-01-01"
                                                    }
                                                />
                                            </div>
                                        </div>
                                        <FormCheck
                                            checks={[
                                                {
                                                    label: "Kawin",
                                                    id: "kawin",
                                                    name: "status",
                                                    value: "Kawin",
                                                    md: 5
                                                },
                                                {
                                                    label: "Belum Kawin",
                                                    id: "belum-kawin",
                                                    name: "status",
                                                    value: "Belum Kawin",
                                                    md: 5
                                                }
                                            ]}
                                            title="Status"
                                            defaultValue={
                                                detailAnggotaKK.status
                                            }
                                        />
                                        <SelectInput
                                            label="Status Perkawinan"
                                            id="status-perkawinan"
                                            name="status_perkawinan"
                                            required
                                            options={[
                                                {
                                                    value: "Menikah",
                                                    text: "Menikah"
                                                },
                                                {
                                                    value: "Belum Menikah",
                                                    text: "Belum Menikah"
                                                },
                                                {
                                                    value: "Cerai Mati",
                                                    text: "Cerai Mati"
                                                },
                                                {
                                                    value: "Cerai Hidup",
                                                    text: "Cerai Hidup"
                                                }
                                            ]}
                                            defaultValue={
                                                detailAnggotaKK.status_perkawinan ||
                                                "default"
                                            }
                                        />
                                        <FormCheck
                                            checks={[
                                                {
                                                    label: "Laki-laki",
                                                    id: "laki-laki",
                                                    name: "jenis_kelamin",
                                                    value: "L",
                                                    md: 5
                                                },
                                                {
                                                    label: "Perempuan",
                                                    id: "perempuan",
                                                    name: "jenis_kelamin",
                                                    value: "P",
                                                    md: 5
                                                }
                                            ]}
                                            title="Jenis Kelamin"
                                            defaultValue={
                                                detailAnggotaKK.jenis_kelamin
                                            }
                                        />
                                        <SelectOptionAgama
                                            defaultValue={detailAnggotaKK.agama}
                                        />
                                        <FormGroup
                                            label="Jenis Pekerjaan"
                                            type="text"
                                            id="pekerjaan"
                                            name="jenis_pekerjaan"
                                            defaultValue={
                                                detailAnggotaKK.jenis_pekerjaan ||
                                                "-"
                                            }
                                        />
                                    </div>
                                    <div className="col-md-6">
                                        <FormGroup
                                            label="Nomor Whatsapp"
                                            type="number"
                                            id="no-whatsapp"
                                            name="no_whatsapp"
                                            defaultValue={
                                                detailAnggotaKK.penduduk
                                                    .no_whatsapp || "0"
                                            }
                                            required={true}
                                        />
                                        <SelectInput
                                            label="Pendidikan"
                                            id="pendidikan"
                                            name="pendidikan"
                                            options={[
                                                {
                                                    text: "SD",
                                                    value: "SD"
                                                },
                                                {
                                                    text: "SMP",
                                                    value: "SMP"
                                                },
                                                {
                                                    text: "SMA",
                                                    value: "SMA"
                                                },
                                                {
                                                    text: "D3",
                                                    value: "D3"
                                                },
                                                {
                                                    text: "S1",
                                                    value: "S1"
                                                },
                                                {
                                                    text: "S2",
                                                    value: "S2"
                                                },
                                                {
                                                    text: "S3",
                                                    value: "S3"
                                                }
                                            ]}
                                        />

                                        <div className="row d-flex gap-2">
                                            <div className="col-md-3">
                                                <SelectInput
                                                    label="RT"
                                                    name="rt"
                                                    options={infoKelurahan.rt.map(
                                                        (rt) => ({
                                                            value: `${rt}`,
                                                            text: `${rt}`
                                                        })
                                                    )}
                                                    defaultValue={
                                                        detailAnggotaKK.alamat
                                                            .rt
                                                    }
                                                />
                                            </div>
                                            <div className="col-md-3">
                                                <SelectInput
                                                    label="RW"
                                                    name="rw"
                                                    options={infoKelurahan.rw.map(
                                                        (rw) => ({
                                                            value: `${rw}`,
                                                            text: `${rw}`
                                                        })
                                                    )}
                                                    defaultValue={
                                                        detailAnggotaKK.alamat
                                                            .rw
                                                    }
                                                />
                                            </div>
                                        </div>
                                        <div className="row d-flex gap-2">
                                            <div className="col-md-5">
                                                <FormGroup
                                                    label="Kelurahan"
                                                    type="text"
                                                    id="kelurahan"
                                                    name="kelurahan"
                                                    defaultValue={
                                                        detailAnggotaKK.alamat
                                                            .kelurahan || "-"
                                                    }
                                                />
                                            </div>
                                            <div className="col-md-5">
                                                <FormGroup
                                                    label="Kecamatan"
                                                    type="text"
                                                    id="kecamatan"
                                                    name="kecamatan"
                                                    defaultValue={
                                                        detailAnggotaKK.alamat
                                                            .kecamatan || "-"
                                                    }
                                                />
                                            </div>
                                        </div>
                                        <div className="row d-flex gap-2">
                                            <div className="col-md-5">
                                                <FormGroup
                                                    label="Kabupaten/Kota"
                                                    type="text"
                                                    id="kabupaten"
                                                    name="kabupaten"
                                                    defaultValue={
                                                        detailAnggotaKK.alamat
                                                            .kabupaten || "-"
                                                    }
                                                />
                                            </div>
                                            <div className="col-md-5">
                                                <FormGroup
                                                    label="Provinsi"
                                                    type="text"
                                                    id="provinsi"
                                                    name="provinsi"
                                                    defaultValue={
                                                        detailAnggotaKK.alamat
                                                            .provinsi || "-"
                                                    }
                                                />
                                            </div>
                                        </div>

                                        <FormTextarea
                                            id="alamat"
                                            label="Alamat"
                                            name="alamat"
                                            defaultValue={
                                                detailAnggotaKK.alamat.alamat ||
                                                "-"
                                            }
                                            rows={5}
                                        />
                                    </div>
                                </div>
                                <ButtonSubmit>
                                    Perbarui Anggota Keluarga
                                </ButtonSubmit>
                            </form>
                        }
                    />
                )}
            </PageContent>
        </>
    )
}

export default EditAnggota
