const useInfoKelurahan = () => {
    const rt: number[] = []
    const rw: number[] = []

    const setDataNumber = (length: number, array: number[]) => {
        for (let i = 1; i <= length; i++) {
            array.push(i)
        }
    }

    setDataNumber(13, rt)
    setDataNumber(3, rw)
    const infoKelurahan = { rt, rw }

    return infoKelurahan
}

export default useInfoKelurahan
